.fouth{
    background-color: #252226;
    height: 500px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}
.plane{
    width: 400px;
    height: 400px;
    opacity: 0.7;
}
.education-info{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    width: 35%;
}
.education-info > p {
    color: white;
    opacity: 0.8;
    font-size: 16px;
}
.education-info > h1 {
    color: white;
    opacity: 0.8;
    -ms-flex-item-align: center;
        align-self: center;
    margin-top: 0;
}

@media screen and (max-width: 711px){
    .fouth{
      -ms-flex-direction: column;
          flex-direction: column;
    }
    .plane{
        width: 200px;
        height: 200px;
    }
    .education-info{
        width: 90%;
        height: inherit;
    }
}
@media screen and (max-width: 452px){
    .fouth{
        height: 550px;
    }

}


