.first{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: #ded1e3 ;
    height: 700px;
}
.photo-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: inherit;
}
.photo-text-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 45%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

}
.photo-text{
    display: -ms-flexbox;
    display: flex;
    height: 100px;
    width: 80%;
    font-size: 35px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    color:  #252226;
    margin-top: 0;
    margin-bottom:0;
}
.about-me{
    color: dimgray;
    text-align: left;
    font-size: 17px;
    width: 80%;
}
.myphoto{
    width: 500px;
    height:500px;
    -ms-flex-negative: inherit;
        flex-shrink: inherit;
}
.menu{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    width: 70%;
    height: 50px;
    margin-bottom: 0;
}
.menu > li{
    padding-left: 3%;
    padding-right: 3%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
    width:170px;
    white-space: nowrap;
    font-size: 16px;
}
.menu > li:hover{
background: #897e8d;
    cursor: pointer;
    list-style: none !important;

}
.menu > li:active{
background: #514b53;
    cursor: pointer;
    list-style: none !important;
}
.menu > li > a {
    vertical-align: middle;
    color: white;
    opacity: 0.9;
    list-style: none !important;
}
.menu > li > a:hover{
    text-decoration: none !important;
}
.menu > li > a:active{
    text-decoration: none !important;
}

.socials{
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 0;
}

.social-net{
    width: 35px;
    height: 35px;
    margin-left: 15px;
    opacity: 0.9;
}
.social-net:hover{
  opacity: 0.6;
}
.social-net:active{
    opacity: 1;
}

.socials-container{
    width: 20%
}
.menu-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    background-color: #252226;
    margin-top: 0;
    height: 50px;
}
/*adaptive rules*/
@media screen and (max-width: 1120px){
    .menu{
        padding-left: 1px;
        margin-left: 0;
        -ms-flex-pack: center;
            justify-content: center;
    }
    .menu-container{
        -ms-flex-pack: center;
            justify-content: center;
    }

    .photo-container{
        height: inherit;
    }


}

@media screen and (max-width: 860px){
    .menu{
        -ms-flex-direction: column;
            flex-direction: column;
        width: 100%;
        height: 100%;
    }

    .menu-container{
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
        height: 300px;
    }
    .menu > li{
        padding-left: 0;
        padding-right: 0;
        height: 50px;
        width:100%;
    }

    .socials{
        height: 50px;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        width:250px;
    }
    .social-net{
        margin-left: 0;
    }
    .myphoto{
        width: 350px;
        height:350px;

    }


}

@media screen and (max-width: 681px){
    .photo-container{
       -ms-flex-direction: column;
           flex-direction: column;
    }
    .first{
        height: 900px;
    }
    .photo-text-container{
        width: 100%;
    }
    .about-me{
        width: 95%;
    }
    .photo-text{
        width: 95%;
        font-size: 28px;
        -ms-flex-pack: center;
            justify-content: center;
    }
}


