@-webkit-keyframes fadeInFromNone {
    0% {

        opacity: 0.3;
    }
    50% {

        opacity: 0.9;
    }
    100% {

        opacity: 0.3;
    }
}

@keyframes fadeInFromNone {
    0% {

        opacity: 0.3;
    }
    50% {

        opacity: 0.9;
    }
    100% {

        opacity: 0.3;
    }
}

.second{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    background-color: #252226;
    height: 500px;
    -ms-flex-align: center;
        align-items: center;
}
.react-logo{
    width: 500px;
    height:500px;
    opacity: 0.6;
    margin-left: 10%;
}
.skills {
    width: 30%;
    height: 70%;
}
.skills-points{

}
.skills-title{
    color: white;
    opacity: 0.8;
    text-align: center;
    margin-bottom: 6%;
    margin-top: 0;
}

.dot{
    width: 22px;
    height: 22px;
    margin-right: 5px;
    opacity: 0.7;
}

.green-dot{
    -webkit-animation: fadeInFromNone 4s ease-out infinite;
            animation: fadeInFromNone 4s ease-out infinite;
}

.html-points,
.js-points,
.react-points,
.reactnative-points,
.redux-points,
.webpack-points,
.git-points {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 100%;
    margin-top: 4%;

}

.html-points > p,
.js-points > p,
.react-points > p,
.reactnative-points > p,
.redux-points > p,
.webpack-points > p,
.git-points > p{
    color: white;
    opacity: 0.8;
    margin-right: 15%;
    font-size: 20px;
    vertical-align: middle;
    margin-bottom: 0;
    white-space: nowrap;
}

/*adaptive rules*/
@media screen and (max-width: 1055px){
    .second{
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
    .react-logo{
        margin-left: 0;
    }

}

@media screen and (max-width: 864px){
    .second{
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
    .react-logo{
        width: 400px;
        height:400px;
    }
}

@media screen and (max-width: 711px){
    .second{
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        height: 600px;
    }
    .skills {
        width: 60%;
    }
}
@media screen and (max-width: 424px){
    .skills {
        width: 100%;
    }
}


